
    export default function MoreLined(options={}){
        if(options.origin){
            return '<svg  width="'+(options.size || 16) +'" height="'+(options.size || 16) +'"   fill="none" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M7 12a2 2 0 11-4 0 2 2 0 014 0z" fill="#1B2129"></path><path d="M14 12a2 2 0 11-4 0 2 2 0 014 0z" fill="#1B2129"></path><path d="M19 14a2 2 0 100-4 2 2 0 000 4z" fill="#1B2129"></path></svg>'
        }
        return '<svg xmlns="http://www.w3.org/2000/svg" width="'+ (options.size || 16) +'" height="'+ (options.size || 16) +'" viewBox="0 0 24 24" aria-hidden="true" fill="'+ (options.color || "currentColor") +'"><path d="M7 12a2 2 0 11-4 0 2 2 0 014 0z"></path><path d="M14 12a2 2 0 11-4 0 2 2 0 014 0z"></path><path d="M19 14a2 2 0 100-4 2 2 0 000 4z"></path></svg>'
    }
