
    export default function ErrorLined(options={}){
        if(options.origin){
            return '<svg  width="'+(options.size || 16) +'" height="'+(options.size || 16) +'"   fill="none" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M13 3.5a1 1 0 10-2 0v13a1 1 0 102 0v-13z" fill="#1B2129"></path><path d="M12 22a1.5 1.5 0 100-3 1.5 1.5 0 000 3z" fill="#1B2129"></path></svg>'
        }
        return '<svg xmlns="http://www.w3.org/2000/svg" width="'+ (options.size || 16) +'" height="'+ (options.size || 16) +'" viewBox="0 0 24 24" aria-hidden="true" fill="'+ (options.color || "currentColor") +'"><path d="M13 3.5a1 1 0 10-2 0v13a1 1 0 102 0v-13z"></path><path d="M12 22a1.5 1.5 0 100-3 1.5 1.5 0 000 3z"></path></svg>'
    }
