
    export default function UserFill(options={}){
        if(options.origin){
            return '<svg  width="'+(options.size || 16) +'" height="'+(options.size || 16) +'"   fill="none" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M16.5 7.5a4.5 4.5 0 11-9 0 4.5 4.5 0 019 0zm-12 10A2.5 2.5 0 017 15h10a2.5 2.5 0 110 5H7a2.5 2.5 0 01-2.5-2.5z" fill="#1B2129" stroke="#1B2129" stroke-width="2"></path></svg>'
        }
        return '<svg xmlns="http://www.w3.org/2000/svg" width="'+ (options.size || 16) +'" height="'+ (options.size || 16) +'" viewBox="0 0 24 24" aria-hidden="true" fill="'+ (options.color || "currentColor") +'"><path d="M16.5 7.5a4.5 4.5 0 11-9 0 4.5 4.5 0 019 0zm-12 10A2.5 2.5 0 017 15h10a2.5 2.5 0 110 5H7a2.5 2.5 0 01-2.5-2.5z"></path></svg>'
    }
