
    export default function SubtractLined(options={}){
        if(options.origin){
            return '<svg  width="'+(options.size || 16) +'" height="'+(options.size || 16) +'"   fill="none" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path fill-rule="evenodd" clip-rule="evenodd" d="M5 12a1 1 0 011-1h12a1 1 0 110 2H6a1 1 0 01-1-1z" fill="#1B2129"></path></svg>'
        }
        return '<svg xmlns="http://www.w3.org/2000/svg" width="'+ (options.size || 16) +'" height="'+ (options.size || 16) +'" viewBox="0 0 24 24" aria-hidden="true" fill="'+ (options.color || "currentColor") +'"><path fill-rule="evenodd" clip-rule="evenodd" d="M5 12a1 1 0 011-1h12a1 1 0 110 2H6a1 1 0 01-1-1z"></path></svg>'
    }
