
    export default function AddLined(options={}){
        if(options.origin){
            return '<svg  width="'+(options.size || 16) +'" height="'+(options.size || 16) +'"   fill="none" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M13 6a1 1 0 10-2 0v5H6a1 1 0 100 2h5v5a1 1 0 102 0v-5h5a1 1 0 100-2h-5V6z" fill="#1B2129"></path></svg>'
        }
        return '<svg xmlns="http://www.w3.org/2000/svg" width="'+ (options.size || 16) +'" height="'+ (options.size || 16) +'" viewBox="0 0 24 24" aria-hidden="true" fill="'+ (options.color || "currentColor") +'"><path d="M13 6a1 1 0 10-2 0v5H6a1 1 0 100 2h5v5a1 1 0 102 0v-5h5a1 1 0 100-2h-5V6z"></path></svg>'
    }
